.info {
  @include page-grid;

  @include content-padding-inline;

  place-items: start center; // вертикаль // горизонталь

  height: auto;
  padding-block: 40px;

  background-image: url(../img/bg-main.png);
  background-repeat: no-repeat;
  background-position: 100% center;
  background-size: cover;

  box-shadow: 0 4px 6px -2px rgba(0, 0, 0, 0.2);

  // text-align: center;

  @include on-tablet {
    align-items: center;
    height: 100vh;
    background-position: 60% center;
  }

  &__slogan {
    grid-column: 1 / -1;

    font-size: 28px;
    font-weight: 700;
    line-height: 1.5;
    color: $main-text-color;

    @include on-tablet {
      grid-column: 1 / 4;
      font-size: 32px;
    }

    @include on-desktop {
      grid-column: 1 / 6;
      font-size: 38px;
    }
  }

  &__text {
    grid-column: 1 / -1;
    font-size: 18px;
    line-height: 1.1;
    color: $main-text-color;

    @include on-tablet {
      grid-column: 5 / 7;
      font-size: 26px;
    }

    @include on-desktop {
      grid-column: 8 / 12;
    }
  }
}
